.contenedor-principal-peliculas {
    margin: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 5px 8px rgba(0, 0, 0, 0.5);
}

.contenedor-peliculas {
    padding: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}


/* cards */
.card {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem;
    background-color: transparent;
    box-shadow: 0px 5px 5px 8px rgba(0, 0, 0, 0.5);
    transition: transform .2s;
}

.card:hover {
    transform: scale(1.3);
  }


.card-body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3em;
    padding: 0;
}


.card-title{
    color: #97a8b2;
    width: 100%;
    text-align: center;
    font-size: 12px;
}





@media  screen and  (max-width: 500px){
    .contenedor-principal-peliculas {
        margin: 1em;
    }

    .card {
        background-color: transparent;
        box-shadow: 0px 5px 5px 8px rgba(0, 0, 0, 0.5);
        transition: none;
        margin: 1rem;
    }
    
    .card:hover {transform: none;}


}

